<script src="../../../../../yijjixhaxun/vue/src/router/index.js"></script>
<template>
    <div id="stayls">
        <headerclass></headerclass>
        <pu-blicto></pu-blicto>
        <div class="Whole">
            <div class="claope">
            	<div class="management">
                    <p class="mentero">设置好应用场景的出参项，建议不要经常修改。如果必须要修改，先把查询数据一键回传后，再修改。修改出参项会影响一键回传功能，修改出参项之前的查询数据如果未回传，则只能单个回传。支持最大入参项20项，支持最大出参项30项。</p>
                </div>
                <p class="fello">应用场景</p>
                <button class="Establish" @click="Establish">创建</button>
                <div class="">
                    <el-table
                            :data="tableData"
                            style="width: 100%">
                        <el-table-column
                                label="序号"
                                width="70"
                            type="index">
                        </el-table-column>
                        <el-table-column
                                prop="sceneName"
                                label="场景名称">
                        </el-table-column>
                        <el-table-column
                                prop="customers"
                                label="适用客户">
                        </el-table-column>
                        <el-table-column
                                prop="time"
                                label="模板">
                            <template slot-scope="scope">
                                <el-button
                                        @click="handleEdit(scope.$index, scope.row)">上传
                                </el-button>
                            </template>
                        </el-table-column>
                        <el-table-column
                                prop="time"
                                label="出参项">
                            <template slot-scope="scope">
                                <el-button
                                        @click="enmakel(scope.$index, scope.row)">编辑
                                </el-button>
                            </template>
                        </el-table-column>
                        <el-table-column
                                prop="download"
                                label="操作">
                            <template slot-scope="scope">
                                <el-button @click="deleteDelete(scope.$index, scope.row)">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
                <div class="paging">
                    <div class="block_plo">
                        <el-pagination
                                @size-change="handleSizeChange"
                                @current-change="handleCurrentChange"
                                :current-page="currentPage"
                                :prev-text="'上一页'"
                                :next-text="'下一页'"
                                :page-count="pageCount"
                                :page-size="pagesize"
                                layout="total, sizes, prev, pager, next, jumper"
                                :total="pageTotal">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>
        <!--创建客户-->
        <div class="Modal">
            <div class="modelto Create" v-if="logop">
                <div class='list'>
                    <div class="Adtion_t">
                        <p>创建场景</p>
                    </div>
                    <div class="sronly-xs">
                        <div class="paccmode">
                            <label for="">场景名称:</label>
                            <el-input
                                    placeholder="请输入场景名称"
                                    v-model="input"
                                    clearable>
                            </el-input>
                        </div>
                        <div class="paccmode">
                            <label for="">模板:</label>
                            <span class="kioser">{{fileto.name}}</span>
                            <a href="javascript:;" class="fellop">
                                <span class="ketle">上传</span>
                                <input type="file" name="file"  ref="info" @change="changeIm($event)" class="flieing"  accept=".xls,.xlsx"/>
                            </a>
                        </div>
                        <div class="form-group plert" style="margin-top: 25px;">
                            <button class="calopi" @click="Eliemker">确定</button>
                            <button class="Cancel" @click="Cancel_cli">取消</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!--删除 -->
        <div class="Modal">
            <div class="modelto delect" v-if="delect">
                <div class='list_de'>
                    <div class="delop">
                        <p>提示</p>
                        <img src="~@assets/img/sop.png" alt="" @click="delectmoer">
                    </div>
                    <div class="deler">
                        <p>是否要删除该信息</p>
                    </div>
                    <div class="sronly-xs">
                        <div class="form-group btuo" style="margin-top: 25px;">
                            <button class="calopi" @click="deleng">确定</button>
                            <button class="Cancel" @click="delecting">取消</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!--模板-->
        <div class="Modal">
            <div class="modelto secret" v-if="secret">
                <div class='list-wll'>
                    <div class="Adtiko">
                        <p>模板</p>
                    </div>
                    <div class="sronly-xs">
                        <div class="tece_le">
                            <label for  ="">选择文件:</label>
                            <span class="kioser" v-if="file">{{file.name}}</span>
                            <a href="javascript:;" class="fellop">
                                <span class="ketle">上传</span>
                                <input type="file" name="contractFileName"  ref="clearFile" @change="changeImage($event)" class="flieing" id="file" accept=".xls,.xlsx"/>
                            </a>
                        </div>
                        <div class="form-group plert" style="margin-top: 30px;">
                            <button class="calopi" @click="Submission">提交</button>
                            <button class="Cancel" @click="calkoe">取消</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!--编辑-->
        <div class="Modal ">
            <div class="modelto keirfo" v-if="modekr">
                <div class='list-mode'>
                    <div class="Adtion_t">
                        <p>出参项</p>
                    </div>
                    <div class="sronl_keo">
                        <ul class="paccopen" id="paccm">
                            <li>
                                <el-input
                                        placeholder=""
                                        v-for="(item, index) in form.dynamicItem" :key="index"
                                        v-model="item.formId"
                                        style="margin-bottom: 15px;"
                                        >
                                </el-input>
                                <img src="~@assets/img/tinsi.png" alt="" v-if="form.dynamicItem && form.dynamicItem.length < 30" @click="entel">
                            </li>
                        </ul>
                        <div class="form-group plert" style="margin-top: 25px;">
                            <button class="calopi" @click="btneo">创建</button>
                            <button class="Cancel" @click="solep">取消</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import headerclass from "@views/Charging/header/header";
    import puBlicto from "@views/Charging/Administration/puBlicto";
    import {getAppList, addAppScenarios,deleteApp,updateAppFile,setAppOutputItems,getAppItemsById} from "@/network/admin";

    export default {
        name: "stayls",
        data() {
            return {
                tableData: [],
                selectedItem: {},
                loading: true,
                currentPage: 1,
                pagesize: 10,
                pageTotal: 0,
                pageCount: 0,
                //分页以上
                accept:'',
                input: '',//创建输入框内容可删除
                money: 0,
                woet: '',
                information: '',
                // Keyinformation:'',
                logop: false,//创建
                delect: false,//删除
                secret: false,//密钥
                file:'',
                fileto:'',
                charing:'',
                experienceData:[],
                forminfos:[],
                form: {
                    dynamicItem: []
                },
                modekr:false,
                conid:[],
                idArr: [],
                appidmo:''
            }
        },
        created: function () {
            this.hadleGetCustomerApi();
        },
        activated:function(){
            this.hadleGetCustomerApi();
        },
        components: {
            headerclass,
            puBlicto,
        },
        methods: {
            handleSizeChange: function (size) {
                this.pagesize = size;
                this.hadleGetCustomerApi();
                var itemsPerPage = this.pagesize;
            },
            // 控制页面的切换
            handleCurrentChange: function (currentPage) {
                this.currentPage = currentPage;
                var currentPage = this.currentPage;
                this.hadleGetCustomerApi();
            },
            //请求列表
            hadleGetCustomerApi() {
                var data = {
                    currentPage: this.currentPage,
                    itemsPerPage: this.pagesize
                };
                getAppList(data).then(res => {
                    this.tableData = res.data.apps;
                    this.pageTotal = res.data.total;
                    this.pageCount = res.data.pages;
                })
                    .catch({});
            },
            //创建
            Establish() {
                this.logop = true;
            },
            //关闭创建
            Cancel_cli() {
                this.input = '';
                this.fileto = ''
                this.logop = false;
            },
            changeIm(e){
                this.fileto = e.target.files[0];
            },
            //创建完成
            Eliemker() {
                if(this.input ==''){
                    layer.msg("场景名称不能为空！", {
                        icon: 2,
                        time: 1500
                    });
                    return false;
                }
                if(this.fileto == ''){
                    layer.msg("请选择文件！", {
                        icon: 2,
                        time: 1500
                    });
                    return false;
                }
                var formData = new FormData();
                formData.append('file', this.fileto);
                let sceneName = this.input;
                formData.append('sceneName', sceneName);
                addAppScenarios(formData).then(res => {
                    if (res.code== 0) {
                        layer.msg(res.msg,{
                            icon: 1,
                            time: 1500
                        });
                        this.logop = false;
                        this.hadleGetCustomerApi();
                        this.input = '';
                        this.fileto = ''
                    } else if(res.code !== 0){
                        layer.msg(res.msg, {
                            icon: 2,
                            time: 1500
                        });
                    }
                })
            },
            //模板
            handleEdit(index, row) {
                this.secret = true;
                // this.Keyinformation = row.custName;
                this.selectedItem = Object.assign({}, row);
            },
            changeImage(e){
                this.file = e.target.files[0];
            },
            //关闭模板
            calkoe(){
                this.secret = false;
                $(".kioser").val('');
                this.file = ''
            },
            //提交模板
            Submission() {
                if(this.file == ''){
                    layer.msg("请选择文件！", {
                        icon: 2,
                        time: 1500
                    });
                    return false;
                }
                let appId = this.selectedItem.appId;
                var formData = new FormData();
                formData.append('file', this.file);
                formData.append('appId', appId);
                updateAppFile(formData).then(res => {
                    if (res.code== 0) {
                        layer.msg(res.msg, {
                            icon: 1,
                            time: 1500
                        });
                        $(".kioser").val('');
                        this.file = ''
                        this.secret = false;
                    } else if(res.code){
                        layer.msg(res.msg, {
                            icon: 2,
                            time: 1500
                        });
                    }
                })

            },
            //删除
            deleteDelete(index, row) {
                this.delect = true;
                this.selectedItem = Object.assign({}, row);
            },
            deleng(){
                let appId = this.selectedItem.appId;
                const data = {
                    appId : appId
                };
                deleteApp(data).then(res => {
                    if (res.code== 0) {
                        layer.msg("删除成功！", {
                            icon: 1,
                            time: 1500
                        });
                        this.delect = false;
                        this.hadleGetCustomerApi();
                    } else if(res.code !== 0){
                        layer.msg(res.msg, {
                            icon: 2,
                            time: 1500
                        });
                    }
                })
            },
            delecting(){
                this.delect = false;
            },
            delectmoer(){
                this.delect = false;
            },
            //编辑
            enmakel(index, row){
                this.appidmo = row.appId;
                var data = {
                    appId :this.appidmo
                }
                getAppItemsById(data).then(res => {
                    if(res.code == 0){
                        this.modekr = true;
                        const list = res.data.items;
                        list.forEach(item => {
                            this.form.dynamicItem.push({
                                formId: item
                            });
                        });
                    } else{
                        layer.msg(res.msg, {
                            icon: 2,
                            time: 1500
                        });
                    }
                })
            },
            entel(){
                this.form.dynamicItem.push({
                    formId: ''
                });
                this.$nextTick(function(){
  					var div = document.getElementById('paccm');
				  	div.scrollTop = div.scrollHeight;
				})
            },
            //关闭编辑
            btneo(){
                const outputItems = [];
                this.form.dynamicItem.forEach(item => {
                    outputItems.push(item.formId);
                });
                if((new Set(outputItems)).size != outputItems.length){
                    layer.msg('出参项不能重复', {
                        icon: 2,
                        time: 1500
                    });
                    return;
                }
                    var data= {
                    appId :this.appidmo,
                    outputItems:outputItems.join(',')
                }
                setAppOutputItems(data).then(res => {
                    if(res.code == 0){
                        layer.msg(res.msg, {
                            icon: 1,
                            time: 1500
                        });
                        this.modekr = false;
                        this.form.dynamicItem = [];
                    } else{
                        layer.msg(res.msg, {
                            icon: 2,
                            time: 1500
                        });
                    }
                })

            },
            solep(){
                this.modekr = false;
                this.form.dynamicItem = [];
            }
        }
    }
</script>

<style>
    @import "~@assets/css/user/application.css";
</style>